<template>
    <div class="box">
        <navbar></navbar>
        <div class="content">
            <div class="center-box warp-1200 ov">
                <centerbar></centerbar>
                <div class="right fl" v-if="setNew">
                    <p class="balance"><span>上传二维码</span></p>
                    <div class="sub-contain pr">
                        <img src="@/static/images/shangchuan_image.png" alt  />
                        <input @change="headImgChange" type="file" accept="image/*" id="upBtn" />
                    </div>
                </div>
                <div class="right fl" v-else>
                    <p class="balance"><span style="cursor:pointer;" @click="setNew = true">重新上传</span></p>
                    <div class="sub-contain pr">
                        <img :src="wechat" alt style="width: 200px"/>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { Navbar, Centerbar } from '@/components'
    import {upwechat} from "../../api/center";
    import { mapGetters } from 'vuex'
    export default {
        name: "Wechat",
        components: {
            Navbar,
            Centerbar
        },
        computed: {
            ...mapGetters([
                'wechat',
            ])
        },
        data(){
            return {
                walletData:{},
                setNew:true
            }
        },
        mounted()
        {
            this.setNew = this.wechat ? false : true
        },
        methods: {
            headImgChange( e ) {

                let file = e.target.files[0]
                /* eslint-disable no-undef */
                let param = new FormData() // 创建form对象
                param.append('file', file, file.name) // 通过a
                upwechat(param).then(res => {

                    this.setNew = false
                    this.$store.dispatch('user/setWechat', res.imgUrl).then(() => {

                        this.$message({
                            message: '上传成功',
                            type: 'success'
                        });
                    }).catch(() => {
                        this.$message({
                            message: '上传失败',
                            type: 'danger'
                        });
                    })
                })
            },
            clearCode()
            {
                this.setNew = true
            }
        }
    }
</script>

<style scoped>
     @import "../../static/css/qrcode.css";
     .box {height: 100%;background: #F2F4F9;}
</style>



